export let value = (value) => {
    switch (typeof value) {
        case "boolean":
            if (value === true) {
                return ""
            } else if (value === false) {
                return ""
            }
        case "string":
            if (value) {
                return value
            }
        case 'object':
            return " "
        default:
            return value
    }
}
